
#treeWrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 20px;
  }
  
  .react-d3-tree .rd3t-node {
    border: 2px solid #4682b4;
    border-radius: 5px;
    padding: 10px;
    background-color: #f0f8ff;
  }
  text{
    text-align: center;
    color: aliceblue;
  }
  .tree-container {
    max-height: 500px;
    overflow-y: auto;
  }
